@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik/Rubik.woff2") format("woff2"), url("../fonts/Rubik/Rubik.woff") format("woff");
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  width: 100%;
  min-width: 32rem;
  margin: 0;
  padding: 0;
  font-family: "Rubik", -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: #000000;
  background-color: #ffffff;
}

body {
  line-height: 1.5;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
}

img {
  height: auto;
  max-width: 100%;
}

label {
  cursor: pointer;
  user-select: none;
}

textarea {
  resize: vertical;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

::-webkit-input-placeholder {
  text-overflow: ellipsis;
}

:-moz-placeholder {
  text-overflow: ellipsis;
}

::-moz-placeholder {
  text-overflow: ellipsis;
}

:-ms-input-placeholder {
  text-overflow: ellipsis;
}

input[placeholder] {
  text-overflow: ellipsis;
  white-space: nowrap;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

.responsive-video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}

.responsive-video iframe,
.responsive-video object,
.responsive-video embed {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.container {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 112.8rem;
}

@media only screen and (max-width: 1023px) {
  .container {
    padding-left: 5.6rem;
    padding-right: 5.6rem;
  }
}

@media only screen and (max-width: 767px) {
  .container {
    padding-left: 1.6rem;
    padding-right: 1.6rem;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.site-wrapper {
  display: flex;
  flex-direction: column;
  font-family: "Rubik", -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1.6rem;
  min-height: 100vh;
}

.site-content {
  position: relative;
  flex: 1 0 auto;
}

.bg-cover {
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.menu-opened {
  overflow: hidden;
}

.menu-opened .site-header__overlay {
  display: block;
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
}

.menu-opened .nav__wrapper {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 8.1rem;
  background-color: transparent;
  overflow-y: scroll;
  z-index: 5;
}

.menu-opened .nav__list {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.menu-opened .nav__item {
  width: 100%;
  margin: 0;
  padding: 1.6rem;
  border-bottom: 1px solid #cecece;
}

.menu-opened .nav__item:last-child {
  border-bottom: none;
}

.menu-opened .nav__link .icon {
  top: 0.4rem;
  transform: none;
}

.menu-opened .btn--burger span {
  opacity: 0;
}

.menu-opened .btn--burger::before {
  width: 3rem;
  right: -0.2rem;
  left: auto;
  top: 1rem;
  transform: rotate(45deg);
  transition: all .2s linear;
}

.menu-opened .btn--burger::after {
  width: 3rem;
  right: -0.2rem;
  left: auto;
  top: 1rem;
  transform: rotate(-45deg);
  transition: all .2s linear;
}

.modal-opened {
  overflow: hidden;
}

.modal-opened .modal__wrapper {
  display: block;
}

.modal-opened .modal {
  animation: bounce 0.6s;
}

@keyframes bounce {
  0% {
    transform: translate(-50%, -50%) translateY(-2000px);
  }
  70% {
    transform: translate(-50%, -50%) translateY(30px);
  }
  90% {
    transform: translate(-50%, -50%) translateY(10px);
  }
  100% {
    transform: translate(-50%, -50%) translateY(0);
  }
}

.btn {
  display: inline-block;
  padding: 1rem 2rem;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  text-align: center;
  border-radius: 2.4rem;
  color: #ffffff;
}

.btn--white {
  border: 1px solid rgba(20, 104, 203, 0.4);
  background-color: #ffffff;
  color: #000000;
  transition: color .2s linear, background .2s linear;
}

.btn--white:hover {
  color: #ffffff;
  background-color: #1468cb;
  border: 1px solid #1468cb;
  transition: color .2s linear, background .2s linear;
}

.btn--circle {
  position: relative;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}

.btn--burger {
  display: none;
  position: relative;
  margin-left: auto;
  width: 2.4rem;
  height: 2.4rem;
  padding-bottom: 2rem;
  background-color: transparent;
  z-index: 10;
  transition: all .2s linear;
}

.btn--burger span {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  left: 0;
  height: 0.2rem;
  width: 100%;
  background-color: #000000;
  border-radius: 1px;
  transition: all .1s linear;
}

.btn--burger::before, .btn--burger::after {
  content: '';
  position: absolute;
  height: 0.2rem;
  width: 100%;
  left: 0;
  right: 0;
  border-radius: 1px;
  background-color: #000000;
  transition: all .2s linear;
}

.btn--burger::before {
  top: 0;
}

.btn--burger::after {
  bottom: 0;
}

@media only screen and (max-width: 767px) {
  .btn--burger {
    display: block;
  }
}

.site-header {
  padding-top: 1.6rem;
  padding-bottom: 1.6rem;
  border-bottom: 1px solid #cecece;
  background-color: #ffffff;
}

.site-header .container {
  max-width: 100%;
  padding-right: 3.2rem;
  padding-left: 3.2rem;
}

@media only screen and (max-width: 1023px) {
  .site-header .container {
    padding-right: 1.6rem;
    padding-left: 1.6rem;
  }
}

.site-header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .nav__wrapper {
    display: none;
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  margin-left: 3.2rem;
}

.nav__item:first-child {
  margin-left: 0;
}

@media only screen and (max-width: 1023px) {
  .nav__item {
    margin-left: 2.4rem;
  }
}

.nav__link {
  position: relative;
  padding-left: 2.4rem;
  display: block;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 2.4rem;
  text-decoration: none;
  color: #000000;
}

.nav__link .icon {
  width: 1.6rem;
  height: 1.6rem;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.nav__text {
  display: block;
}

.nav__text--long {
  display: block;
}

.nav__text--short {
  display: none;
}

@media only screen and (max-width: 1023px) {
  .nav__text--long {
    display: none;
  }
  .nav__text--short {
    display: block;
  }
}

.logo {
  max-width: 10.8rem;
}

.logo__link {
  display: block;
}

.logo img {
  display: block;
}

.info {
  padding-top: 4.8rem;
