@media only screen and (max-width: 1023px) {
  .modal {
    max-width: 85.5%;
  }
}

@media only screen and (max-width: 767px) {
  .modal {
    padding: 1.6rem;
    max-width: 100%;
  }
}

.modal__wrapper {
  position: fixed;
  display: none;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 5.6rem;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 15;
}

.modal__title {
  margin-top: 0;
  margin-bottom: 3.2rem;
  font-weight: bold;
  font-size: 3.2rem;
  line-height: 4.0rem;
  color: #000000;
}

@media only screen and (max-width: 1023px) {
  .modal__title {
    margin-bottom: 2.4rem;
  }
}

@media only screen and (max-width: 767px) {
  .modal__title {
    margin-bottom: 1.6rem;
    font-size: 2.4rem;
    line-height: 3.2rem;
  }
}

.modal__btn-close {
  position: absolute;
  width: 4.8rem;
  height: 4.8rem;
  top: 0;
  right: -7.4rem;
  background-color: #ffffff;
}

.modal__btn-close .icon {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 2.4rem;
  height: 2.4rem;
}

@media only screen and (max-width: 1023px) {
  .modal__btn-close {
    top: -7.4rem;
    right: 0;
  }
}

@media only screen and (max-width: 767px) {
  .modal__btn-close {
    top: auto;
    bottom: -6.4rem;
    right: 0;
    left: 0;
    margin: 0 auto;
  }
}

.applicant__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.applicant__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;
  width: 100%;
  border-top: 1px solid #e1e1e1;
  background-color: #ffffff;
}

.applicant__item:last-child {
  border-bottom: none;
}

.applicant__link {
  display: flex;
  justify-content: space-between;
}

.applicant__content {
  padding-right: 1.6rem;
}

.applicant__name {
  margin-top: 0;
  margin-bottom: 0.4rem;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

.applicant__details-wrapper {
  display: flex;
}

.applicant__details {
  display: flex;
  margin-left: 2.5rem;
  font-weight: normal;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #616161;
}

.applicant__details:first-child {
  margin-left: 0;
}

@media only screen and (max-width: 767px) {
  .applicant__details--ticket-number {
    display: none;
  }
}

.applicant__data-info {
  margin-left: 0.5rem;
}

.applicant__btn {
  padding: 0.6rem 1.6rem;
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #1468cb;
  text-decoration: none;
}

.phone {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 48rem;
}

@media only screen and (max-width: 767px) {
  .phone {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

@media only screen and (max-width: 767px) {
  .phone__item {
    margin-bottom: 2.4rem;
  }
  .phone__item:last-child {
    margin-bottom: 0;
  }
}

.phone__heading {
  margin-top: 0;
  margin-bottom: 0.4rem;
  font-weight: normal;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #6f6f6f;
}

.phone__number {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 2.0rem;
  line-height: 2.8rem;
  color: #000000;
  text-decoration: none;
}

.social {
  width: 100%;
  max-width: 26.5rem;
}

@media only screen and (max-width: 1023px) {
  .social {
    max-width: 21.6rem;
  }
}

.social__list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  list-style: none;
  margin: 0;
  padding: 0;
}

.social__link {
  position: relative;
  width: 4.8rem;
  height: 4.8rem;
  display: block;
  border: 1px solid #cecece;
  transition: all .2s linear;
}

.social__link .icon {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  transition: all .2s linear;
}

.social__link--fb .icon {
  width: 1.2rem;
  height: 2.3rem;
}

.social__link--vk .icon {
  width: 2.3rem;
  height: 1.4rem;
}

.social__link--tw .icon {
  width: 2.3rem;
  height: 1.9rem;
}

.social__link--inst .icon {
  width: 2.3rem;
  height: 2.3rem;
}

.social__link:hover {
  transition: all .2s linear;
  border-color: #1468cb;
  transform: scale(1.1);
}

.social__link:hover .icon {
  transition: all .2s linear;
  transform: scale(1.1);
  fill: #1468cb;
}

.site-footer {
  border-top: 1px solid #e1e1e1;
  background-color: #ffffff;
}

@media only screen and (max-width: 767px) {
  .site-footer__phones {
    margin-bottom: 2.4rem;
  }
}

.site-footer__social {
  margin-left: auto;
}

@media only screen and (max-width: 1023px) {
  .site-footer__social {
    margin-left: 4rem;
  }
}

@media only screen and (max-width: 767px) {
  .site-footer__social {
    margin-left: 0;
  }
}

.site-footer__top {
  display: flex;
  justify-content: space-between;
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;
  border-bottom: 1px solid #e1e1e1;
}

@media only screen and (max-width: 767px) {
  .site-footer__top {
    flex-direction: column;
    align-items: flex-start;
  }
}

.site-footer__top-wrapper {
  border-bottom: 1px solid #e1e1e1;
}

.site-footer__bottom {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;
}

@media only screen and (max-width: 767px) {
  .site-footer__bottom {
    flex-direction: column;
  }
}

.site-footer__logo {
  max-width: 12.4rem;
  margin-left: auto;
}

@media only screen and (max-width: 767px) {
  .site-footer__logo {
    margin-left: 0;
  }
}

.site-footer__text {
  font-size: 1.2rem;
  line-height: 1.6rem;
}

@media only screen and (max-width: 1023px) {
  .site-footer__text {
    max-width: 48rem;
  }
}

@media only screen and (max-width: 767px) {
  .site-footer__text {
    margin-bottom: 2.4rem;
  }
}

.site-footer__copy {
  margin-top: 0;
  margin-bottom: 0.8rem;
}

@media only screen and (max-width: 767px) {
  .site-footer__copy {
    margin-bottom: 2.4rem;
  }
}

.site-footer__offer {
  margin-top: 0;
  margin-bottom: 0;
}

.site-footer__link-offer {
  color: #1468cb;
  text-decoration: none;
}
